import React from 'react';

class Counter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      count: 0,
    };
  }

  render() {
    return (
      <div>
        <p>{this.state.count}</p>
        <p>
          <button
            onClick={() => this.setState(({ count }) => ({ count: count + 1 }))}
          >
            Count
          </button>
        </p>
      </div>
    );
  }
}

export default Counter;
